@-webkit-keyframes entryAnimation {
    0% { opacity: 0; -webkit-transform: translate(-40px); transform: translate(-40px) }
    50% { opacity: 0.5; -webkit-transform: translate(-20px); transform: translate(-20px) }
    100% { opacity: 1; -webkit-transform: translate(0px); transform: translate(0px) }
}

@keyframes entryAnimation {
    0% { opacity: 0; -webkit-transform: translate(-40px); transform: translate(-40px) }
    50% { opacity: 0.5; -webkit-transform: translate(-20px); transform: translate(-20px) }
    100% { opacity: 1; -webkit-transform: translate(0px); transform: translate(0px) }
}

@-webkit-keyframes entryAnimationDown {
    0% { opacity: 0; -webkit-transform: translateY(-30px); transform: translateY(-30px) }
    50% { opacity: 0.5; -webkit-transform: translateY(-15px); transform: translateY(-15px) }
    100% { opacity: 1; -webkit-transform: translateY(0px); transform: translateY(0px) }
}

@keyframes entryAnimationDown {
    0% { opacity: 0; -webkit-transform: translateY(-30px); transform: translateY(-30px) }
    50% { opacity: 0.5; -webkit-transform: translateY(-15px); transform: translateY(-15px) }
    100% { opacity: 1; -webkit-transform: translateY(0px); transform: translateY(0px) }
}

